
.top-image {
    width: 100%;
}
.text-container {
    position: relative;
}
.text-over {
    position: absolute;
    top: 30%;
    left: 20%;
    -webkit-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
}
@media (max-width:768px) {
    .text-over {
        font-size: 21px;
        left: 5%;
        top: 15%;
    }
}


/*Contacts form*/
.form-block {
    text-align: left;
}
.form-block button {
    float: right;
}
.fullWidth-img {
    width: 100%;
}


