.nav-link {
    color:#003333;
}
.img-logo {
    width:100%;
}
.menuBtn:hover {
    color: white;
    background-color: #33cc99;
    opacity: 0.6;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-radius: 2px;

}
.menuBtn {
    -webkit-transition: width 2s; /* Safari */
    -webkit-transition: ease 0.6s;
    -o-transition: ease 0.6s;
    transition: ease 0.6s;
}


@media (max-width: 768px) {
#hamburger-menu {
   float: right;
   margin: 16px 8px 0 0;
   width:50px;
   height: 50px;
   cursor: pointer;
}
.icon-bar {
    display: block;
    height: 5px;
    width: 30px;
    background-color: #003333;
    border-radius: 1px;
    -webkit-transition: all 0.4s ease-in-out;
    -o-transition: all 0.4s ease-in-out;
    transition: all 0.4s ease-in-out;
    margin: 2px auto;

}
#hamburger-menu.is-active:nth-child(2) {
    opacity: 0;
}
/* menu */
.menu-navigation {
    display: none;
}

}


