.stepsImage {
    height: 150px;
    margin-bottom: 10%;
}
div .stepsImage:hover {
    -webkit-transform: scale(1.1);
    -khtml-transform: scale(1.1);
    -ms-transform: scale(1.1);
    transform: scale(1.1);
    -webkit-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
}




